import { Component, OnInit, HostListener } from '@angular/core';
import { ShowListService } from '../services/show-list.service';
import { upload } from '../models/upload.model';
import { AuthService } from '../services/auth.service';
import { desk } from '../models/desk.model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-show-list',
  templateUrl: './show-list.component.html',
  styleUrls: ['./show-list.component.css']
})
export class ShowListComponent implements OnInit {

  desks: desk[] = [];
  page: number = 0;
  loading: boolean = false;
  constructor(private showListService: ShowListService, private router: Router, private authService: AuthService) { }
 

  ngOnInit() {
    this.showListService.showDeskList()
      .subscribe((data: desk[]) => {
        this.desks = data['desks'];
        // this.sortDesks(this.desks);
      },
      (err) => {
        console.log(err);
      })
  }

  uploadContent() {
    window.location.href = 'http://localhost:3000/auth/google';
  }

  detailDeskInfo(desk) {
    this.router.navigate(['/detail'], { queryParams: { 
      filename: desk.filename, 
      username: desk.username,
      idea: desk.idea,
      location: desk.location,
      createdTime: desk.createdTime } });
  }

  @HostListener('window:scroll') scrolling(){
    if (Math.ceil(window.innerHeight + window.scrollY) >= document.body.offsetHeight) {
      this.loading = true;
      this.page++;
      this.showListService.showDeskListMore(this.page)
      .subscribe((data: desk[]) => {
        this.desks = this.desks.concat(data['desks']);
        this.loading = false;
      },
      (err) => {
        console.log(err);
      })
    }
  }
}



