import { Component, OnInit } from '@angular/core';
import { upload } from '../models/upload.model';
import { WriteContentService } from '../services/write-content.service';
import { Router, ActivatedRoute } from '@angular/router';

declare var $: any;

@Component({
  selector: 'app-write-content',
  templateUrl: './write-content.component.html',
  styleUrls: ['./write-content.component.css']
})

export class WriteContentComponent implements OnInit {
  imageSrc: string;
  upload: upload ={
    idea: null,
    location: null
  };

  googleId: string;
  userName: string;

  constructor(private writeService: WriteContentService, private router: Router, private route: ActivatedRoute) { }

  ngOnInit() {
    this.googleId = this.route.snapshot.queryParamMap.get('googleid');
    this.userName = this.route.snapshot.queryParamMap.get('username');
  }
  
  onFileChanged(event) {
    const file = event.target.files[0]
    let reader = new FileReader();

    reader.onload = (e) => {
      this.imageSrc= e.target['result'];
    };
    reader.readAsDataURL(file);
  }

  onClickSubmit(files: FileList, upload: upload) {
    const formData = new FormData();
    formData.append('googleid', this.googleId);
    formData.append('username', this.userName);
    formData.append('idea', upload.idea);
    formData.append('location', upload.location);
    formData.append('userfile', files[0]);

    this.writeService.uploadImage(formData)
    .subscribe((data: any) =>{
      if(data.message === 'success'){
        this.router.navigate(['/list']);
      }
    })
  }
}



